.training__container {
  position: relative;

  h2 {
    font-size: 35px;
    font-weight: 800;
    line-height: 100%;
    margin: 53px 0 32px 0;

    @include vp-1199 {
      font-size: 25px;
      font-weight: 800;
      line-height: 25px;
      margin: 16px 0 22px;
    }

    @include vp-767 {
      font-size: 22px;
      line-height: 22px;
      margin: 22px 0 16px 0;
    }
  }

  h3 {
    font-size: 21px;
    font-weight: 700;
    line-height: 21px;
    margin: 0 0 53px 0;

    @include vp-1199 {
      margin: 0 0 62px 0;
    }

    @include vp-767 {
      margin: 0 0 56px;
    }
  }

  h5 {
    font-size: 21px;
    font-weight: 700;
    line-height: 21px;
    margin: 0 0 29px;
    max-width: 250px;

    @include vp-1199 {
      margin: 0 0 39px;
      font-size: 18px;
      line-height: 18px;
      max-width: 200px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 16px;
      margin: 0 0 21px;
    }
  }

  p {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    max-width: 820px;
    margin: 0 0 60px;

    @include vp-1199 {
      margin: 0 0 28px;
      max-width: 560px;
    }

    @include vp-767 {
      max-width: 320px;
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.training__title-shadow {
  font-size: 55px;
  font-style: normal;
  font-weight: 800;
  line-height: 55px;
  color: #2d383f;
  opacity: 0.03;
  position: absolute;
  top: -17px;
  left: 120px;

  @include vp-1199 {
    top: -11px;
    left: 45px;
    font-size: 40px;
    line-height: 40px;
  }

  @include vp-767 {
    left: 15px;
    top: -4px;
    max-width: 159px;
    overflow: hidden;
    font-size: 26px;
    font-weight: 800;
    line-height: 26px;
  }
}

.training__swiper-button-wrapper {
  position: absolute;
  right: 120px;
  top: 193px;
  display: flex;
  gap: 4px;

  @include vp-1199 {
    right: 45px;
    top: 166px;
  }

  @include vp-767 {
    right: 14px;
    top: 204px;
  }
}

.training__button-prev,
.training__button-next {
  font-size: 0;
  background-color: $color-default-blue;
  border: none;
  cursor: pointer;

  @include hover-focus {
    border: 1px solid #1c3374;
    background-color: transparent;
  }

  @include active {
    background-color: #1c3374;
  }

  @include disabled {
    cursor: not-allowed;
    pointer-events: none;
    outline: none;
    background-color: #999999;
  }

  svg {
    color: $color-default-white;

    @include hover-focus {
      color: #1c3374;
    }
  }
}

.training__button-prev {
  left: -79px;
}

.training__button-next {
  transform: rotate(180deg);
  right: 0;
}

.training__swiper {
  margin: 0 auto 56px;
  max-width: 1200px;

  @include vp-1199 {
    margin: 0 auto 26px;
    max-width: 768px;

  }

  @include vp-767 {
    margin: 0 auto 25px;
    max-width: 320px;
  }
}

.training__slide {
  position: relative;
  object-fit: cover;
  padding: 15px;
  border: 1px solid rgba(2, 102, 193, 0.1);
  display: flex;
  flex-wrap: wrap;

  @include vp-767 {
    max-width: 320px;
  }

  h4 {
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    max-width: 118px;
    margin: 0 0 15px;
  }

  img {
    flex-shrink: 0;
    margin: 0 0 15px;
    object-fit: cover;

    @include vp-1199 {
      width: 268px;
      height: 200px;
    }

    @include vp-767 {
      width: 290px;
      height: 200px;
    }
  }

  p {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    margin: 0;
    min-height: 64px;

    @include vp-1199 {
      min-height: 96px;
    }

    @include vp-767 {
      min-height: 60px;
    }
  }
}

.training__slide-social {
  cursor: pointer;
  margin: 0 0 0 auto;
}

.training__process-list {
  display: flex;
  list-style-type: none;
  padding: 0 0 56px;
  margin: 0;

  @include vp-1199 {
    flex-wrap: wrap;
    gap: 92px 74px;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 25px;
  }
}

.training__process-item {
  display: flex;
  gap: 10px;
  align-items: flex-start;
  position: relative;
  padding: 0 10px;

  @include vp-1199 {
    padding: 0 0 0 10px;
  }

  @include vp-767 {
    gap: 13px;
    padding: 0 0 0 30px;
    align-items: center;
  }

  img {
    @include vp-767 {
      width: 21px;
      height: 19px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #0266c1;
    left: 10px;
    bottom: -15px;

    @include vp-1199 {
      display: none;
    }

    @include vp-767 {
      display: block;
      width: 13px;
      height: 13px;
      left: 0px;
      bottom: 10px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #0266c1;
    left: 10px;
    bottom: -8px;
    opacity: 0.2;

    @include vp-1199 {
      display: none;
    }

    @include vp-767 {
      display: block;
      width: 1px;
      height: 55px;
      background-color: #0266c1;
      left: 6px;
      bottom: -41px;
      opacity: 0.2;
    }
  }

  &--map {
    @include vp-1199 {
      order: 1;
    }
  }

  &--boots {
    @include vp-1199 {
      order: 2;
    }
  }

  &--tree {
    @include vp-1199 {
      order: 4;
    }

    @include vp-767 {
      order: 3;
    }
  }

  &--mountain {
    @include vp-1199 {
      order: 3;
    }

    @include vp-767 {
      order: 4;
    }
  }
}

.training__process-item:last-child::after {
  width: 330px;
  @include vp-1199 {
    display: none;
  }
}

.training__process-description {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  max-width: 198px;

  @include vp-1199 {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    max-width: 225px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 18px;
    max-width: 201px;
  }
}
